import React, { useEffect, useState } from "react";
import { TXDevToolsProps } from "./types";
import { ErrorComponent, ErrorBoundary } from "./ErrorComponent";
import { Switch } from "./Switch";
import { Collapsable } from "./Collapsable";

import { CollapsableWrapper } from "./CollapsableWrapper";

export const DevTools = ({
  store = {},
  XIconPosition = { bottom: "50px", right: "50px" },
  keepOpen = false,
  iconColor = "rgb(233 62 44)",
  hideIcon = false,
  maxLogCount = 15,
  disableToggleESCKey = false,
}: TXDevToolsProps) => {
  const [showTools, setShowTools] = useState(keepOpen || false);
  const [count, setCount] = useState(0);

  useEffect(() => {
    function handleKeyPress(event: any) {
      if (event.key === "Escape") {
        setShowTools(keepOpen || !showTools);
      }
    }

    if (!disableToggleESCKey) {
      window.addEventListener("keydown", handleKeyPress);
      return () => {
        window.removeEventListener("keydown", handleKeyPress);
      };
    }
  }, [showTools, setShowTools]);

  return (
    <ErrorBoundary Error={ErrorComponent}>
      <div id="react-store-explorer-container">
        {" "}
        <div
          id="usex-devtools"
          style={{
            zIndex: 1000000000,
            height: "100%",
            width: "420px",
            maxWidth: "100%",
            position: "fixed",
            // background: "rgb(250,250,250)",
            background: "white",
            boxSizing: "border-box",
            transition: "right 0.2s ",
            top: 0,
            right: showTools ? "0px" : "-420px",
            color: "#444",
            overflow: "auto",
            boxShadow: "rgb(202 204 204) 0px 0px 10px 0px",

            fontFamily:
              '-apple-system, BlinkMacSystemFont, "Segoe UI","Ubuntu", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji"',
          }}
        >
          <div
            style={{
              textAlign: "center",
              background: "white",
              borderLeft: "1px solid #CCC",
              padding: "10px",
            }}
          >
            <span style={{ fontWeight: "bold", fontSize: "18px" }}>
              react-store-explorer
            </span>
          </div>

          <ErrorBoundary Error={ErrorComponent}>
            {Object.keys(store).map((key: any) => {
              const stateValue = store[key];
              return stateValue &&
                !!stateValue.get &&
                !!stateValue.subscribe &&
                !!stateValue.unsubscribe ? (
                <ErrorBoundary Error={ErrorComponent}>
                  <div key={key}>
                    <CollapsableWrapper
                      maxLogCount={maxLogCount}
                      stateValue={stateValue}
                      name={key}
                    />
                  </div>
                </ErrorBoundary>
              ) : (
                <></>
              );
            })}
          </ErrorBoundary>

          {Object.keys(store).length === 0 && (
            <div style={{ textAlign: "center", marginTop: "10px" }}>
              {" "}
              <i>Store is Empty</i>
            </div>
          )}
        </div>
        {!hideIcon && (
          <div
            onMouseDown={() => {
              setShowTools(keepOpen || !showTools);
            }}
            id="usex-devtools-holder"
            style={{
              zIndex: 1000000001,
              width: "50px",
              height: "50px",
              background: "green",
              borderRadius: "50px",
              position: "fixed",
              boxShadow: "0px 0px 10px 1px #CCC",
              cursor: "pointer",
              ...XIconPosition,
            }}
          >
            <div
              id="react-store-explorer-holder"
              style={{
                width: "50px",
                height: "50px",
                background: iconColor,
                borderRadius: "50px",
                position: "relative",
                boxShadow: "0px 0px 10px 1px #CCC",
                cursor: "pointer",
              }}
            >
              <span
                style={{
                  borderRadius: "5px",
                  border: "1px solid white",
                  background: "white",
                  width: "5px",
                  height: "5px",
                  left: "21.5px",
                  top: "10px",
                  position: "absolute",
                }}
              ></span>
              <span
                style={{
                  borderRadius: "5px",
                  border: "1px solid white",
                  background: "white",
                  width: "5px",
                  height: "5px",
                  left: "12px",
                  top: "30px",
                  position: "absolute",
                }}
              ></span>
              <span
                style={{
                  borderRadius: "5px",
                  border: "1px solid white",
                  background: "white",
                  width: "5px",
                  height: "5px",
                  left: "32px",
                  top: "30px",
                  position: "absolute",
                }}
              ></span>
            </div>
          </div>
        )}
      </div>
    </ErrorBoundary>
  );
};
